## Layout
*/
.root {
  box-sizing: border-box;
  overflow: hidden;
  position: relative;
  top: 0;
  left: 0;
}

.pc_container,
.ipc_container,
.pc_tb_container,
.container {
  width: 100%;
  box-sizing: border-box;
  padding-left: 50px;
  padding-right: 50px;
  margin-left: auto;
  margin-right: auto;
  max-width: 1260px;
  box-sizing: border-box;
}
.pc_container._relative,
.ipc_container._relative,
.pc_tb_container._relative,
.container._relative {
  position: relative;
  top: 0;
  left: 0;
}
.pc_container._md,
.ipc_container._md,
.pc_tb_container._md,
.container._md {
  width: 100%;
  box-sizing: border-box;
  padding-left: 50px;
  padding-right: 50px;
  margin-left: auto;
  margin-right: auto;
  max-width: 1100px;
}
.pc_container._sm,
.ipc_container._sm,
.pc_tb_container._sm,
.container._sm {
  width: 100%;
  box-sizing: border-box;
  padding-left: 50px;
  padding-right: 50px;
  margin-left: auto;
  margin-right: auto;
  max-width: 600px;
}
.pc_container._sm2,
.ipc_container._sm2,
.pc_tb_container._sm2,
.container._sm2 {
  width: 100%;
  box-sizing: border-box;
  padding-left: 50px;
  padding-right: 50px;
  margin-left: auto;
  margin-right: auto;
  max-width: 300px;
}

.pc_padding,
.padding {
  padding-left: 50px;
  padding-right: 50px;
}

.main_flex_area {
  position: relative;
  top: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  flex-direction: row-reverse;
}
.main_flex_area > * {
  min-width: 0;
  max-width: 100%;
  width: 100%;
}
.main_flex_area._department_detail {
  padding-top: 20px;
}

.main_column {
  order: 1;
  width: 100%;
  box-sizing: border-box;
  padding-left: 60px;
}
@media screen and (max-width: 1199px) {
