  cursor: default;
  color: inherit;
}
.root .tel_link, .root .tel_link:hover,
.root .tel_link-no,
.root .tel_link-no:hover {
  text-decoration: none;
  color: inherit;
}

.sp_click,
.pc_no_click {
  pointer-events: none;
  cursor: default;
}

