/**************************

## - 段組設定 2列 3列 4列

PCの時だけ、中央に等間隔で余白を作り、width: 100%; を守って列になる。
ul でなくてもいい。

**2列 .pc_column2**

	<ul class="pc_column2">
		<li>コンテンツ</li>
		<li>コンテンツ</li>
		<li>コンテンツ</li>
		<li>コンテンツ</li>
		<li>コンテンツ</li>
	</ul>

**3列 .pc_column3**

	<ul class="pc_column3">
		<li>コンテンツ</li>
		<li>コンテンツ</li>
		<li>コンテンツ</li>
		<li>コンテンツ</li>
		<li>コンテンツ</li>
	</ul>

**4列 .pc_column4**

	<ul class="pc_column4">
		<li>コンテンツ</li>
		<li>コンテンツ</li>
		<li>コンテンツ</li>
		<li>コンテンツ</li>
		<li>コンテンツ</li>
	</ul>

*/
.pc_column2 {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
  margin-bottom: -20px;
  width: calc(100% + 20px);
}
.pc_column2 > * {
  padding-left: 10px;
  padding-right: 10px;
  box-sizing: border-box;
  width: 50%;
  margin-bottom: 20px;
}

.pc_column3 {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
  margin-bottom: -20px;
  width: calc(100% + 20px);
}
.pc_column3 > * {
  padding-left: 10px;
  padding-right: 10px;
  box-sizing: border-box;
  width: 33.33%;
  margin-bottom: 20px;
}

.pc_column4 {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
  margin-bottom: -20px;
  width: calc(100% + 20px);
}
.pc_column4 > * {
  padding-left: 10px;
  padding-right: 10px;
  box-sizing: border-box;
  width: 25%;
  margin-bottom: 20px;
}

.pc_column2b::before, .pc_column2b::after {
  content: "";
  display: table;
}
.pc_column2b::after {
  clear: both;
}
.pc_column2b > * {
  width: 50%;
  float: left;
  box-sizing: border-box;
}
.pc_column2b > *:nth-child(2n+1) {
  clear: left;
  padding-right: 15px;
}
.pc_column2b > *:nth-child(2n) {
  padding-left: 15px;
}

.pc_column3b::before, .pc_column3b::after {
  content: "";
  display: table;
}
.pc_column3b::after {
  clear: both;
}
.pc_column3b > * {
  width: 33.3333333333%;
  float: left;
  box-sizing: border-box;
}
.pc_column3b > *:nth-child(3n+1) {
  clear: left;
  padding-right: 13px;
}
.pc_column3b > *:nth-child(3n+2) {
  padding-left: 6px;
  padding-right: 6px;
}
.pc_column3b > *:nth-child(3n) {
  padding-left: 13px;
}

.pc_column4b::before, .pc_column4b::after {
  content: "";
  display: table;
}
.pc_column4b::after {
  clear: both;
}
.pc_column4b > * {
  width: 25%;
  float: left;
  box-sizing: border-box;
}
.pc_column4b > *:nth-child(4n+1) {
  clear: left;
  padding-right: 15px;
}
.pc_column4b > *:nth-child(4n+2) {
  padding-left: 5px;
  padding-right: 10px;
}
.pc_column4b > *:nth-child(4n+3) {
  padding-left: 10px;
  padding-right: 5px;
}
.pc_column4b > *:nth-child(4n) {
  padding-left: 15px;
}

.pc_column2 img,
.pc_column3 img,
.pc_column4 img {
  display: block;
  margin-left: auto;
  margin-right: auto;
  vertical-align: bottom;
}

.set {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.set > * {
  flex-grow: 5;
}
.set > *:not(.fix) {
  margin-top: -10px;
}
.set > .left {
  order: 1;
}
.set > .right {
  order: 2;
}
.set > .fix {
  flex-shrink: 0;
  flex-grow: 0;
  width: 100%;
}
.set > .flex {
  display: flex;
}

