## Project

特定ページ専用、効果範囲が限定的な記述。
コンテンツ内容に密接で、汎用性がなく、機能の付け替えを想定していない。

*/
body._fixed {
  overflow: hidden;
}

.header__inner {
  height: 80px;
}
.header__inner .close_btn,
.header__inner .open_btn {
  display: none;
}
.header__r {
  display: flex;
  align-items: center;
  column-gap: 30px;
}
@media screen and (max-width: 1199px) {
  .header__r {
    column-gap: 20px;
  }
}
.header .gnav__ul {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  column-gap: 28px;
}
@media screen and (max-width: 1199px) {
  .header .gnav__ul {
    column-gap: 15px;
  }
}
.header .gnav__a:hover .gnav__text::after {
  transform: translateX(-50%) scale(1);
}
.header .gnav__text {
  color: #4d4741;
  font-size: 1.125rem;
  letter-spacing: 0.1em;
  position: relative;
}
@media screen and (max-width: 1199px) {
  .header .gnav__text {
    font-size: 1rem;
  }
}
.header .gnav__text::after {
  content: "";
  display: inline-block;
  width: 100%;
  height: 1px;
  background-color: #4d4741;
  position: absolute;
  bottom: -5px;
  left: 50%;
  transform: translateX(-50%) scale(0, 1);
  transform-origin: center top;
  transition: transform cubic-bezier(0.645, 0.045, 0.355, 1) 0.3s;
}
.header .contact_btn a {
  width: 188px;
  display: block;
  background-color: #ed8767;
  border-radius: 20px;
  padding: 6px 10px;
  text-align: center;
  box-sizing: border-box;
  border: 1px solid transparent;
  transition: background-color 0.2s;
}
.header .contact_btn a:hover {
  background-color: white;
  border: 1px solid #ed8767;
}
.header .contact_btn a:hover .text {
  color: #ed8767;
}
.header .contact_btn a:hover .text::before {
  background-position-y: -16px;
}
.header .contact_btn a .text {
  color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
  letter-spacing: 0.1em;
}
.header .contact_btn a .text::before {
  content: "";
  display: inline-block;
  background: url("../img/icon/mail_white.webp") 0 0 no-repeat transparent;
  background-size: 23px 31px;
  width: 23px;
  height: 16px;
  margin-right: 10px;
}